import React from "react";
import { useState, useEffect } from "react";
import Button from "./components/Button";
import NewNoteForm from "./components/NewNoteForm";
import Note from "./components/Note";
import Notes from "./components/Notes";
import noteService from "./services/notes";
import Notification from "./components/Notification";
import Footer from "./components/Footer";

const App = () => {
  const [notes, setNotes] = useState([]);
  const [note, setNote] = useState('');
  const [newNote, setNewNote] = useState('');
  const [showAll, setShowAll] = useState(true);
  const [errorMessage, setErrorMessage] = useState(null);

  const hook = () => {
    const promise = noteService.getAll();
    const eventHandler = (initialNotes) => {
      setNotes(initialNotes);
    };
    promise.then(eventHandler);
  };
  useEffect(hook, []);

  const addNote = (event) => {
    event.preventDefault();
    const noteObject = {
      content: newNote,
      date: new Date(),
      important: Math.random() < 0.5
    };
    const promise = noteService.create(noteObject);
    const eventHandler = (returnedNote) => {
      setNotes(notes.concat(returnedNote));
      setNewNote("");
    };
    promise.then(eventHandler);
  };

  const toggleImportanceOf = (id) => {
    const note = notes.find((noteToToggle) => noteToToggle.id === id);
    const changedNote = { ...note, important: !note.important };
    const promise = noteService.update(id, changedNote);
    const eventHandler = (returnedNote) => {
      setNotes(notes.map((note) => (note.id !== id ? note : returnedNote)));
    };
    /*
    const errorHandler = (error) => {
      setErrorMessage(`Note '${note.content}' was already removed from server`);
      setTimeout(() => {
        setErrorMessage(null);
      }, 5000);
      setNotes(notes.filter((n) => n.id !== id));
    };*/
    promise.then(eventHandler) //.catch(errorHandler);
  };

  const handleNoteChange = (event) => {
    setNewNote(event.target.value);
  };

  const notesToShow = showAll ? notes : notes.filter((note) => note.important);
  const buttonText = showAll ? "show important" : "show all";

  return (
    <div>
      <h1>Notes</h1>
      <Notification message={errorMessage} />
      <Button onClick={() => setShowAll(!showAll)} text={buttonText} />
      <ul>
        {notesToShow.map((note) => (
          <Note
            key={note.id}
            note={note}
            toggleImportance={() => toggleImportanceOf(note.id)}
          />
        ))}
      </ul>
      {/*<Notes notes={notesToShow} 
                   selectedNote={note}
                   toggleImportanceOf={toggleImportanceOf(selectedNote.id)}
      />*/}
      <NewNoteForm
        onSubmit={addNote}
        value={newNote}
        onChange={handleNoteChange}
      />
      <Footer />
    </div>
  );
};

export default App;
